<template>
  <div class="portfolio-root">
    <div class="title-container">
      <h2>Portfolio</h2>
    </div>
    <div class="product-container">
      <div class="product product-1" @click="toggleHatchQDetail()">
        <div class="title">HatchQ</div>
        <div class="cover">
          <img src="~@/assets/hatchq/hatchq-cover.png">
        </div>
      </div>
      <div class="product product-2" @click="toggleBincentiveDetail()">
        <div class="title">Bincentive</div>
        <div class="cover">
          <img src="~@/assets/bincentive-mainsite/bincentive-mainsite-cover.png">
        </div>
      </div>
      <div class="product product-3" @click="toggleMedisinfoDetail()">
        <div class="title">Medisinfo2u</div>
        <div class="cover">
          <img src="~@/assets/medisinfo/medisinfo-cover.png">
        </div>
      </div>
    </div>
    <hatch-q v-if="showHatchQDetail" @close="toggleHatchQDetail()" />
    <bincentive v-if="showBincentiveDetail" @close="toggleBincentiveDetail()" />
    <medisinfo v-if="showMedisinfoDetail" @close="toggleMedisinfoDetail()" />
  </div>
</template>

<script>
import HatchQ from '@/components/ProductDetail/HatchQ.vue';
import Bincentive from '@/components/ProductDetail/Bincentive.vue';
import Medisinfo from '@/components/ProductDetail/Medisinfo.vue';
export default {
  name: 'Portfolio',
  components: {
    HatchQ,
    Bincentive,
    Medisinfo
  },
  data() {
    return {
      showHatchQDetail: false,
      showBincentiveDetail: false,
      showMedisinfoDetail: false,
    }
  },
  methods: {
    toggleHatchQDetail() {
      this.showHatchQDetail = !this.showHatchQDetail;
    },
    toggleBincentiveDetail() {
      this.showBincentiveDetail = !this.showBincentiveDetail;
    },
    toggleMedisinfoDetail() {
      this.showMedisinfoDetail = !this.showMedisinfoDetail;
    }
  },
  mounted() {
  },
}
</script>
<style lang="scss" scoped>
@import "@/theme/theme.scss";
.portfolio-root {
  width: 100%;
  min-height: 100vh;
  background-color: $portfolio-darkblue;
  .title-container {
    margin-left: 10%;
    animation: fadein 2s;
    -moz-animation: fadein 2s;
    -webkit-animation: fadein 2s;
    -o-animation: fadein 2s;
    @media (max-width: $ipad-size) {
      margin-left: 0;
      margin: auto;
      text-align: center;
    }
  }
  h2 {
    margin: 0;
    padding-top: 50px;
    color: $dark-main-text;
    // text-shadow: 0px 0px 9px rgba(225,225,225, 0.6);
    font-weight: normal;
    font-size: 60px;
  }
  .product-container {
    display: flex;
    margin: 80px 10% 0;
    align-items: center;
    justify-content: space-between;
    width: 80%;
    box-sizing: border-box;
    animation-fill-mode: forwards;
    -moz-animation-fill-mode: forwards;
    -o-animation-fill-mode: forwards;
    -webkit-animation-fill-mode: forwards;
    animation: fadeinplus 3s;
    -moz-animation: fadeinplus 3s;
    -webkit-animation: fadeinplus 3s;
    -o-animation: fadeinplus 3s;
    padding-bottom: 80px;
    @media (max-width: $small-screen-size) {
      flex-wrap: wrap;
      margin: 80px auto 0;
      width: 90%;
    }
    @media (max-width: $ipad-size) {
      flex-direction: column;
      margin: 20px auto 0px;
      padding-bottom: 40px;
    }
    .product {
      width: 300px;
      height: 300px;
      border-radius: 50%;
      position: relative;
      cursor: pointer;
      -webkit-transform: perspective(1px) translateZ(0);
      transform: perspective(1px) translateZ(0);
      box-shadow: 0 0 1px rgba(0, 0, 0, 0);
      -webkit-transition-duration: 0.3s;
      transition-duration: 0.3s;
      @media (max-width: $small-screen-size) {
        width: 250px;
        height: 250px;
        margin: 10px 0;
      }
      .title {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        font-size: 40px;
        font-weight: 600;
        color: #0f0f0f;
      }
      .cover {
        max-width: 200px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        position: relative;
        opacity: 0;
        z-index: -1;
        -webkit-transition-duration: 0.3s;
          transition-duration: 0.3s;
        &:after {
          position:absolute;
          content:'';
          width:100%;
          height:100%;
          top:0;
          left:0;
        }
        img {
          width: 100%;
        }
      }
      &.product-1 {
        background-color: #faca02;
        .cover::after {
          box-shadow:0 0 30px 20px #faca02 inset;
        }
      }
      &.product-2 {
        background-color: #003247;
        .title {
          color: #FAFDF6;
        }
        .cover img {
          border-radius: 16px;
        }
        .cover::after {
          box-shadow:0 0 20px 30px #003247 inset;
        }
      }
      &.product-3 {
        background-color: #d3bd76;
        .cover::after {
          box-shadow:0 0 30px 20px #d3bd76 inset;
        }
      }
      &:hover {
        position: relative;
        transform: scale(1.2);
        -webkit-transition-timing-function: cubic-bezier(0.47, 2.02, 0.31, -0.36);
        transition-timing-function: cubic-bezier(0.47, 2.02, 0.31, -0.36);
        .title {
          -webkit-transition-duration: 0.3s;
          transition-duration: 0.3s;
          text-shadow: 0px 0px 9px rgba(225,225,225, 1);
        }
        .cover {
          opacity: 0.7;
          -webkit-transition-duration: 0.3s;
          transition-duration: 0.3s;
        }
      }
    }
  }
}
@keyframes fadein {
    0% {
        opacity:0;
    }
    100% {
        opacity:1;
    }
}
@-moz-keyframes fadein { /* Firefox */
    0% {
        opacity:0;
    }
    100% {
        opacity:1;
    }
}
@-webkit-keyframes fadein { /* Safari and Chrome */
    0% {
        opacity:0;
    }
    100% {
        opacity:1;
    }
}
@-o-keyframes fadein { /* Opera */
    0% {
        opacity:0;
    }
    100% {
        opacity: 1;
    }
}
@keyframes fadeinplus {
    0% {
        opacity:0;
    }
    20% {
      opacity: 0;
    }
    100% {
        opacity:1;
    }
}
@-moz-keyframes fadeinplus { /* Firefox */
    0% {
        opacity:0;
    }
    20% {
      opacity: 0;
    }
    100% {
        opacity:1;
    }
}
@-webkit-keyframes fadeinplus { /* Safari and Chrome */
    0% {
        opacity:0;
    }
    20% {
      opacity: 0;
    }
    100% {
        opacity:1;
    }
}
@-o-keyframes fadeinplus { /* Opera */
    0% {
        opacity:0;
    }
    20% {
      opacity: 0;
    }
    100% {
        opacity: 1;
    }
}
</style>


