import Vue from 'vue'
import Router from 'vue-router'
import Main from '@/components/Main'
import Portfolio from '@/components/Portfolio'

Vue.use(Router)

export default new Router({
  // mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Main',
      component: Main
    },
    {
      path: '/Portfolio',
      name: 'Portfolio',
      component: Portfolio
    }
  ]
})



