<template>
  <div class="main-root">
    <!-- の工作空間 -->
    <h1>Ena's Workspace</h1>
    <div class="section-container">
      <button class="portfolio-button" @click="openPortfolio()">Portfolio</button>
      <button class="trial-button disabled" @mouseover="showComingSoon=true" @mouseleave="showComingSoon=false">{{ showComingSoon ? 'Coming Soon' : 'Trial Space' }}</button>
    </div>
    <div id="background" class="background"></div>
  </div>
</template>

<script>

export default {
  components: {
  },
  data() {
    return {
      showComingSoon: false,
    }
  },
  computed: {
  },
  methods: {
    openPortfolio() {
      let bg = document.getElementById('background');
      bg.style.background = '#0B132B';
      bg.style.minHeight = '100vh';
      setTimeout(() => {
        window.scrollTo(0,0)
        this.$router.push({name: 'Portfolio'})
      }, 300); 
    }
  },
  created() {
  }
}
</script>

<style lang="scss" scoped>
@import "@/theme/theme.scss";
.main-root {
  position: relative;
  background-color: #0f0f0f;
  width: 100%;
  min-height: 100vh;
  text-align: center;
  .background {
    background-color: transparent;
    width: 100%;
    min-height: 0;
    position: absolute;
    top: 0;
    left: 0;
    padding: 20px;
    transition: ease-in-out 0.2s;
    z-index: 4;
  }
  h1 {
    margin: 0;
    padding-top: 50px;
    color: $dark-main-text;
    text-shadow: 0px 0px 9px rgba(225,225,225, 0.6);
    font-size: 60px;
    @media (max-width: $below-ipad-size) {
      font-size: 48px;
    }
  }
  .rectangle-container {
    position: relative;
    &::before {
      position: absolute;
      content: '';
      width: 250px;
      height: 250px;
      background-color: $dark-transparent-glass;
      z-index: 2;
      left: 20px;
      top: 0;
    }
    &::after {
      position: absolute;
      content: '';
      width: 250px;
      height: 250px;
      background-color: $wood;
      left: 00px;
      top: 20px;
    }
  }
  .section-container {
    margin-top: 80px;
    @media (max-width: $below-ipad-size) {
      margin-top: 60px;
      padding-bottom: 60px;
    }
  }
  .portfolio-button {
    width: 300px;
    height: 280px;
    color: $dark-text-white;
    font-size: 40px;
    background-color: $portfolio-darkblue;
    padding: 50px;
    border-radius: 50%;
    cursor: pointer;
    transition: ease-in-out 0.5s;
    position: relative;
    z-index: 2;
    -webkit-transform: perspective(1px) translateZ(0);
    transform: perspective(1px) translateZ(0);
    box-shadow: 0 0 1px rgba(0, 0, 0, 0);
    -webkit-transition-duration: 0.3s;
    transition-duration: 0.3s;
    text-shadow: 0px 0px 9px rgba(225,225,225, 0.6);
    box-shadow:0px 0px 0px 0px $portfolio-darkblue;
    &::after {
      transition: ease-in-out 0.3s;
      position: absolute;
      content: '';
      width: 0px;
      height: 0px;
      background-color: $portfolio-darkblue-half;
      z-index: -1;
      top: 50%;
      left: 50%;
      transform: translate(-50%,-50%);
      border-radius: 50%;
      pointer-events: none;
      opacity: 0;
    }
    &:hover {
      position: relative;
      z-index: 2;
      transform: scale(1.2);
      // -webkit-transition-timing-function: cubic-bezier(0.47, 2.02, 0.31, -0.36);
      // transition-timing-function: cubic-bezier(0.47, 2.02, 0.31, -0.36);
      box-shadow:7px 7px 6px 30px $portfolio-darkblue ;
    }
  }
  .trial-button {
    width: 300px;
    height: 280px;
    color: $dark-text-white;
    font-size: 40px;
    background-color: $workspace-darkpurple;
    padding: 50px;
    border-radius: 50%;
    cursor: pointer;
    transition: ease-in-out 0.5s;
    position: relative;
    // z-index: 2;
    -webkit-transform: perspective(1px) translateZ(0);
    transform: perspective(1px) translateZ(0);
    box-shadow: 0 0 1px rgba(0, 0, 0, 0);
    -webkit-transition-duration: 0.3s;
    transition-duration: 0.3s;
    text-shadow: 0px 0px 9px rgba(225,225,225, 0.6);
    &::after {
      transition: ease-in-out 0.3s;
      position: absolute;
      content: '';
      width: 0px;
      height: 0px;
      background-color: $workspace-darkpurple-half;
      z-index: -1;
      top: 50%;
      left: 50%;
      transform: translate(-50%,-50%);
      border-radius: 50%;
      pointer-events: none;
      opacity: 0;
    }
    &:hover {
      position: relative;
      // z-index: 2;
      transform: scale(1.2);
      -webkit-transition-timing-function: cubic-bezier(0.47, 2.02, 0.31, -0.36);
      transition-timing-function: cubic-bezier(0.47, 2.02, 0.31, -0.36);
    }
    &.disabled {
      transform: none;
      opacity: 0.5;
      cursor: not-allowed;
    }
  }
  // .portfolio-button:hover ~ .background {
  //   min-height: 100vh;
  //   background-color: $portfolio-darkblue-half;
  // }
}
</style>


