<template>
  <div 
    class="lightbox-container"
    @click.self="closeLightbox()"
    @keydown.esc="closeLightbox()">
    <span class="close-button" @click="closeLightbox()"><img src="@/assets/close-icon.png" /></span>
    <div class="product-container">
      <h2 class="product-title">
        HatchQ
      </h2>
      <img class="product-cover" src="~@/assets/hatchq/hatchq-cover.png">
      <div class="description-title">
        Description
      </div>
      <p class="product-description">
        A complete website that display event information, and conduct mini-games.
        <br>build up using framework Vue.js, HTML and CSS.
        <br>highly responsive and mobile friendly. Compiled using SCSS.
        <br>transfer and communicate data with backend using API
      </p>
      <div class="description-title">
        Showcase
      </div>
      <div class="link">
        <a href="https://enayee.github.io/eventwebsitehatchqdemo/#/" target="_blank">Visit Demo</a>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HatchQ',
  methods: {
    closeLightbox() {
      this.$emit('close')
    }
  }
}
</script>

<style lang="scss" scoped>
@import "@/theme/theme.scss";
.lightbox-container {
  z-index: 9999;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  overflow: auto;
}
.close-button {
  position: fixed;
  top: 12px;
  right: 12px;
  cursor: pointer;
}
.product-container {
  text-align: center;
  margin: 80px auto;
  width: 80%;
  max-width: 880px;
  overflow: auto;
  @media (max-width: $below-ipad-size) {
    margin: 40px auto;
  }
  .product-title {
    font-size: 46px;
    color: #faca02;
    @media (max-width: $below-ipad-size) {
      font-size: 24px;
    }
  }
}
.description-title {
  text-align: left;
  font-size: 28px;
  font-weight: 700;
  margin: 40px 0 20px;
}
.product-description {
  text-align: left;
  font-size: 24px;
  color: #faca02;
  @media (max-width: $below-ipad-size) {
    font-size: 18px;
  }
}
.product-cover {
  width: 100%;
  max-width: 880px;
}
</style>


